@use "../../common/forwarded.scss" as f;

.feedback {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 60px;
  padding: 35px 0 0;
  width: 100%;
  box-sizing: border-box;
}

.feedback__title {
  margin-bottom: 25px;
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
  color: f.$color-text-second;
  text-align: center;
}

.feedback__subtitle {
  margin-bottom: 35px;
  padding: 0 30px;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  color: f.$color-text-second;
  text-align: center;
}

.feedback__form {
  padding: 30px 30px 40px;
  box-sizing: border-box;
  background-color: f.$color-background-second;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 15px;
}

.feedback__list {
  @include f.list-reset;
  margin-bottom: 43px;
}

.feedback__item {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  color: f.$color-text-second;
}

.feedback__libel {
  margin-bottom: 12px;
  font-weight: 700;
  text-align: center;
}

.feedback__input {
  padding: 18px;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  border: none;
  border-radius: 8px;
  resize: none;

  &--massage {
    min-height: 260px;
  }
}

.feedback__item:not(:last-child) {
  margin-bottom: 12px;
}

.feedback__button {
  margin-left: auto;
  margin-right: auto;
}
